.tooltip:after,.tooltip:before{
  opacity:0;
  transition-property:background-color, border-color, color, fill, stroke, opacity, box-shadow, transform;
  transition-duration:.15s;
  transition-duration:.2s;
  transition-timing-function:cubic-bezier(.4, 0, .2, 1);
  transition-delay:.1s;
}

.tooltip:after{
  position:absolute;
  content:"";
  width:0;
  height:0;
  display:block;
  transform:translateX(-50%);
  border:var(--tooltip-tail, 0) solid transparent;
  border-top:var(--tooltip-tail, 0) solid var(--tooltip-color);
  top:auto;
  left:50%;
  right:auto;
  bottom:var(--tooltip-tail-offset);
}

.tooltip.tooltip-open:after,.tooltip.tooltip-open:before,.tooltip:hover:after,.tooltip:hover:before{
  opacity:1;
  transition-delay:75ms;
}

.tooltip-bottom:after{
  transform:translateX(-50%);
  border-left-color:transparent;
  border-bottom-color:var(--tooltip-color);
  border-right-color:transparent;
  border-top-color:transparent;
  top:var(--tooltip-tail-offset);
  left:50%;
  right:auto;
  bottom:auto;
}

.tooltip-left:after{
  transform:translateY(-50%);
  border-left-color:var(--tooltip-color);
  border-bottom-color:transparent;
  border-right-color:transparent;
  border-top-color:transparent;
  top:50%;
  left:auto;
  right:calc(var(--tooltip-tail-offset) + 1px);
  bottom:auto;
}

.tooltip-right:after{
  transform:translateY(-50%);
  border-left-color:transparent;
  border-bottom-color:transparent;
  border-right-color:var(--tooltip-color);
  border-top-color:transparent;
  top:50%;
  left:calc(var(--tooltip-tail-offset) + 1px);
  right:auto;
  bottom:auto;
}

